import React, { useState, useEffect, useRef } from "react";
import { findDOMNode } from "react-dom";

import ReactPlayer from "react-player";
import Channels from "./channels";

import "./styles.scss";
const baseUrl = `https://tv4.live/api/stream`;

export default function App() {
  const [currentVideo, setCurrentVideo] = useState(
    window.localStorage.getItem("v")
  );
  const [username, setUsername] = useState(
    window.localStorage.getItem("u") || ""
  );
  const [password, setPassword] = useState(
    window.localStorage.getItem("p") || ""
  );

  const videoUrl = `${baseUrl}/${username}/${password}/${currentVideo}`;

  useEffect(() => {
    window.localStorage.setItem("u", username);
    window.localStorage.setItem("p", password);
    window.localStorage.setItem("v", currentVideo);
  });

  console.log(videoUrl);
  return (
    <div className="App">
      <div id="video" style={{ width: "100%", maxHeight: "50vh" }}>
        <ReactPlayer url={videoUrl} playing controls />
      </div>
      <Channels currentVideo={currentVideo} setCurrentVideo={setCurrentVideo} />
      <input
        type="text"
        placeholder="username"
        value={username}
        onChange={(ev) => {
          setUsername(ev.target.value);
        }}
      />
      <input
        type="password"
        placeholder="password"
        value={password}
        onChange={(ev) => {
          setPassword(ev.target.value);
        }}
      />
    </div>
  );
}
