export default function channels(props) {
  const { currentVideo, setCurrentVideo } = props;
  const channels = [
    "livetv.epg/sport1.il.m3u8",
    "livetv.epg/sport2.il.m3u8",
    "livetv.epg/sport3.il.m3u8",
    "livetv.epg/sport4.il.m3u8",
    "livetv.epg/sport5.4k.il.m3u8",
    "livetv.epg/5sporthd.il.m3u8",
    "livetv.epg/5gold.il.m3u8",
    "livetv.epg/5live.il.m3u8",
    "livetv.epg/5plus.il.m3u8",
    "livetv.epg/5starts.il.m3u8"
  ];
  return (
    <ul>
      {channels.map((url) => {
        const name = url.substring(
          url.lastIndexOf("/") + 1,
          url.lastIndexOf(".il.m3u8")
        );
        return (
          <li key={name} className={currentVideo === url ? "selected" : ""}>
            <a
              href="#video"
              onClick={() => {
                if (currentVideo === url) {
                  window.open(url);
                } else {
                  setCurrentVideo(url);
                }
              }}
            >
              {name}
            </a>
          </li>
        );
      })}
    </ul>
  );
}
